import React from 'react';

import {
  ContainerWrapper,
  ContentWrapper,
  HeadSection,
  AbilityTitle,
  LineWrap,
  Linehr,
  DevelopSection,
  ContentDevWrapper,
  ScreenSection,
  Screen,
  Editor,
  DevContent,
  DevContentTitle,
  DevContentText,
  DevContentsmallText,
  OrgSection,
  ContentOrgWrapper,
  Screen2,
  Editor2,
  OrgContent,
  OrgContentTitle,
  OrgContentText,
  OrgContentsmallText,
  ScreenSection2,
} from "./abilityStyles";
import ScreenImg from "../../assets/images/other/section1/MacBookPro1.svg";
import editorImg from "../../assets/images/other/section1/Frame8.svg";
import ImageEditor from "../../assets/images/other/section1/BrowserWindow.svg";
import ImageScreen from "../../assets/images/other/section1/Rectangle81.svg";
const Ability = () => {
    return (
      <ContainerWrapper>
        <ContentWrapper>
          <HeadSection>
            <AbilityTitle>What I do</AbilityTitle>
            <LineWrap>
              <Linehr />
            </LineWrap>
          </HeadSection>

          <DevelopSection>
            <ContentDevWrapper>
              <ScreenSection>
                <Screen src={ScreenImg} alt="" />
                <Editor src={editorImg} alt="" />
              </ScreenSection>
              <DevContent>
                <DevContentTitle>Software Development</DevContentTitle>
                <DevContentText>
                  I am a very passionate developer and follower of the software
                  craftsmen community. I value good practices and clean code.
                </DevContentText>

                <DevContentsmallText>Mobile Web, APIs</DevContentsmallText>
              </DevContent>
            </ContentDevWrapper>
          </DevelopSection>

          <OrgSection>
            <ContentOrgWrapper>
              <OrgContent>
                <OrgContentTitle>Organisation</OrgContentTitle>
                <OrgContentText>
                  I am avid believer of the impact of technology in the business
                  space.
                </OrgContentText>

                <OrgContentsmallText>
                  Partner projects, collaboration
                </OrgContentsmallText>
              </OrgContent>
              <ScreenSection2>
                <Screen2 src={ImageScreen} alt="" />
                <Editor2 src={ImageEditor} alt="" />
              </ScreenSection2>
            </ContentOrgWrapper>
          </OrgSection>
        </ContentWrapper>
      </ContainerWrapper>
    );
}

export default Ability
