import styles from "styled-components";

export const ContainerWrapper = styles.section`
background: #0A093D;
display:flex;
align-items:center;
 width: 100vw;
min-height: 746px;
  overflow: hidden;

@media all and (min-width:768px){
     width: 100vw;
    overflow: hidden;
}
@media all and (min-width:1366px){
  background: #0A093D;
display:flex;
align-items:center;
width:100%;
min-height: 746px;
 margin: auto;
   overflow: hidden;
}
`;

export const ContentWrapper = styles.div`

@media all and (max-width: 412px) {
    display: grid;
    
    grid-gap: 2rem;
    width: 100%;
    height: 100%;
    margin-left: 0px;
   min-height: 1000px;
}

@media (min-width: 413px) and (max-width: 768px){
    display: grid;
   
    grid-gap: 1rem;
    width: 100%;
    height: 100%;
    margin-left: 0px;
   min-height: 1000px;
}

@media (min-width:769px) and (max-width: 947px){
    display: grid;
   
    grid-gap: 1rem;
    width: 100%;
    height: 100%;
}

@media (min-width: 948px) and (max-width: 1017px){
      display: grid;
   justify-items:center;
    grid-gap: 1rem;
    width: 100%;
    height: 100%;
}
@media (min-width: 1018px) and (max-width: 1365px){
    width: 100%;
    height: 100%;
}


@media all and (min-width: 1366px){
     width: 976px;
height: 100%;
margin:auto;
height: 100%;

}
`;

export const HeadSection = styles.div`

display: flex;
width:100%;

@media all and (max-width: 412px){
    display: block;
  text-align: center;
  justify-content: center;
  margin-bottom: 10px;
}

@media (min-width: 413px) and (max-width: 768px){
    display: block;
  text-align: center;
  justify-content: center;
  margin-bottom: 20px;
}
@media (min-width: 769px) and (max-width: 907px){
      display: block;
  text-align: center;
  justify-content: center;
  margin-bottom: 30px;
}

@media (min-width: 908px) and (max-width: 1017px){
 display: flex;
 margin-bottom: 30px;
  justify-content: center;
}
@media (min-width: 1018px) and (max-width: 1365px){
   display: flex;
 margin-bottom: 30px;
  justify-content: center;
}

@media all and (min-width: 1366px){
 margin-bottom: 30px;
}




`;

export const WorkTitle = styles.h1`
color: white;
margin-right: 20px;
font-size: 44px;
`;

export const LineWrap = styles.div`
width: 740px;
position: relative;
top: 2.5rem;
margin: 0;
padding: 0;

@media all and (max-width:1160px){
    width: 70%;
}
@media all and (max-width: 1116px){
    width: 68%;
  
}
@media all and (max-width: 907px){
    display: none;
}
@media all and (max-width: 768px){
    display: none;
}
@media all and (max-width: 412px){
   display: none;
}
`;

export const Linehr = styles.hr`
border-color:#C4C4C4;
`;

export const BoxWrapper = styles.div`
background: rgba(242, 15, 34, 0.22);
position:absolute;
width:100%;
height:100%;
display:none;
&.active {
display:flex;
}
`;
export const BoxFooter = styles.div`
display:flex;
position:relative;
align-items:flex-end;
padding-left:20px;
bottom: 10px;


`;
export const MyWorkwrapper = styles.div`
padding: 20px 0;
width: 100%;
min-height:100%;
display:flex;
flex-direction:column;
align-items:center;

@media all and (min-width: 1366px){
 

width: 100%;
margin-top:50px;
}
`;

export const Mywork = styles.div`
padding: 0;
margin-right: 40px;
width: 261.91px;
height: 349.88px;
background: rgba(138, 147, 174, 0.3);

@media all and (max-width: 768px){
  margin-right: 0px;
}
@media all and (max-width: 412px){
  margin-right: 0px;
}
`;

export const Links = styles.div`
display: flex;
justify-content: flex-end;
margin-top: 16px;
margin-bottom: 3px;
padding-right: 10px;

`;

export const LinkA = styles.a`
cursor: pointer;
outline: none;
z-index: 1;
display: flex;
align-items: center;
`;

export const GithubLink = styles.svg`
cursor: pointer;
margin-right: 20px;
z-index: 1;

:hover {
    transition: 0.2s all ease-in-out;
    g path {
        fill: red;
    }

    
}

`;
export const LinkB = styles.a`
cursor: pointer;
outline: none;
z-index: 1;
display: flex;
align-items: center;
`;
export const WebLink = styles.svg`
cursor: pointer;
:hover {
    transition: 0.2s all ease-in-out;
    path {
        stroke: red;
    }

    
}
`;
export const ImageWrapper = styles.div`
background: ${({ colorA }) => {
  if (colorA === "BB9570") {
    return "#BB9570";
  } else if (colorA === "F9B428") {
    return "#F9B428";
  } else {
    return "#07C1DE";
  }
}};
width: 231.89px;
height: 196.52px;
position: relative;
top: 3.81px;
left:16.51px;
margin-bottom: 20px;
display: flex;
align-items: center;
justify-content: center;
`;
export const ImagesView = styles.img`

`;

export const MyWorkTitle = styles.h3`
color: #CED7F3;
font-size: 19.65px;
margin-left: 15px;
margin-top: 18.8px;
`;

export const MyWorkTech = styles.p`
color: #8A93AE;
font-size: 13.36px;
margin-left: 15px;
margin-top: 35.37px;
`;
