import React from "react";
import {
  FormControl,
  FormLabel,
  Input,
  Textarea,
  FormErrorMessage,
  SimpleGrid,
  GridItem,
  Button,
  Box,
  VisuallyHidden,
} from "@chakra-ui/react";

const ContactForm = ({ errors, touched, submit, Field }) => {
  return (
    <Box as="form" onSubmit={submit} w="full">
      <SimpleGrid rowGap={4} w="full">
        <GridItem colSpan={1}>
          <FormControl id="name" isInvalid={errors.name && touched.name}>
            <VisuallyHidden>
              <FormLabel>Name</FormLabel>
            </VisuallyHidden>

            <Field
              as={Input}
              placeholder="Name"
              borderColor="rgba(242, 15, 34, 0.6)"
              borderWidth="2px"
              h={"50px"}
              bg="rgba(255, 255, 255, 0.2)"
              color="#ffffff"
              type="text"
              name="name"
            />

            <FormErrorMessage>
              {errors.name && touched.name && errors.name}
            </FormErrorMessage>
          </FormControl>
        </GridItem>

        <GridItem colSpan={1}>
          <FormControl isInvalid={errors.number && touched.number}>
            <VisuallyHidden>
              <FormLabel>PhoneNumber</FormLabel>
            </VisuallyHidden>

            <Field
              as={Input}
              bg="rgba(255, 255, 255, 0.2)"
              color="#ffffff"
              borderWidth="2px"
              borderColor="rgba(242, 15, 34, 0.6)"
              placeholder="Phone Number"
              h={"50px"}
              type="text"
              name="number"
            />
            <FormErrorMessage>
              {errors.number && touched.number && errors.number}
            </FormErrorMessage>
          </FormControl>
        </GridItem>

        <GridItem colSpan={1}>
          <FormControl id="email" isInvalid={errors.email && touched.email}>
            <VisuallyHidden>
              <FormLabel>Email</FormLabel>
            </VisuallyHidden>

            <Field
              as={Input}
              bg="rgba(255, 255, 255, 0.2)"
              color="#ffffff"
              borderWidth="2px"
              borderColor="rgba(242, 15, 34, 0.6)"
              placeholder="Email"
              h={"50px"}
              type="email"
              name="email"
            />
            <FormErrorMessage>
              {errors.email && touched.email && errors.email}
            </FormErrorMessage>
          </FormControl>
        </GridItem>

        <GridItem colSpan={1}>
          <FormControl
            id="message"
            isInvalid={errors.message && touched.message}
          >
            <VisuallyHidden>
              <FormLabel>Message</FormLabel>
            </VisuallyHidden>

            <Field
              as={Textarea}
              bg="rgba(255, 255, 255, 0.2)"
              color="#ffffff"
              borderWidth="2px"
              borderColor="rgba(242, 15, 34, 0.6)"
              h="135px"
              placeholder="How can I help?"
              type="text"
              name="message"
            />

            <FormErrorMessage>
              {errors.message && touched.message && errors.message}
            </FormErrorMessage>
          </FormControl>
        </GridItem>

        <GridItem colSpan={1}>
          <Box align="right">
            <Button
              bg="#F20F22"
              colorScheme="#ffffff"
              size="lg"
              width={["full", "full", "full", "160px"]}
              align="center"
              fontWeight="light"
              type="submit"
            >
              Submit
            </Button>
          </Box>
        </GridItem>
      </SimpleGrid>
    </Box>
  );
};

export default ContactForm;
