import styles from "styled-components";
import { Link as RouterLink } from "react-router-dom";
import { Link as ScrollLink } from "react-scroll";

export const NavWraps = styles.div`
position:relative;
width:100%;
  height: 80px;

  @media all and (max-width: 768px) {
     height: 10vh;
     width: 100vw;
     overflow:hidden;
     transition: 0.3s all ease;
}
@media screen and (max-width: 412px){
     height: 10vh;
     width: 100vw;
     overflow:hidden;
}
@media screen and (max-width: 374px){
     height: 50px;
}

`;
export const Nav = styles.nav`
  background: #0A093D;
  width:100%;
  height: 80px;
  display: flex;
  justify-content: center;
  position: fixed;
  align-items: center;
  top: 0;
  z-index: 10;
  transition: all 0.2s ease-in;
   overflow:hidden;

   
@media all and (max-width: 768px) {
     height: 10vh;
     width: 100vw;
     overflow:hidden;
     transition: 0.3s all ease;
}
@media screen and (max-width: 412px){
     height: 10vh;
     width: 100vw;
     overflow:hidden;
}
@media screen and (max-width: 374px){
     height: 50px;
}


@media screen and (max-width: 960px){
      transition: 0.8s all ease;
  }
`;

export const NavbarContainer = styles.div`
display: flex;
justify-content: space-between;
height: 80px;
z-index: 1;
width:100vw;
padding: 0 24px;
margin-right: 37px;
@media screen and (max-width: 374px){
     width: 100%;
     height: 50px;
}

@media all and (max-width: 768px){
     height: 100%;
     width: 100%;
}
@media all and (max-width: 412px){
     height: 100%;
     width: 100%;
     
}


`;

export const NavLogo = styles(RouterLink)`
color: #fff;
justify-self: flex-start;
cursor: pointer;
display: flex;
align-items:center;
margin-left: 24px;
font-weight: bold;
text-decoration: none;
z-index: 4;

@media only screen and (max-width: 394px){
     margin-left: 0px;
     
}
@media all and (max-width: 768px) {
     margin-left: 0px;
}
@media screen and (max-width: 412px){
     margin-left: 0px;
     
}
`;
export const LogoImage = styles.img`
object-fit:contain;
height: 55px;

@media all and (max-width: 768px) {
     height: 5vh;
     margin-left: 0;
     object-fit:contain;
}

@media all and (max-width: 412px){
     height: 5vh;
     margin-left: 0;
      
}

@media screen and (max-width: 374px){
     height: 30px;
}
@media screen and (max-width: 394px){
     height: 40px;
     object-fit:contain;
}
`;

export const MobileIcon = styles.div`
display: none;
@media screen and (max-width: 768px) {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 5vh;
    font-size: 1.8rem;
    cursor: pointer;
    color: #fff;
    transform: translate(-100%, 60%);
}

@media all and (max-width: 412px){
     bottom: 5vh;
     
}
@media screen and (max-width: 390px) {
    bottom: 3rem; 
   
}
@media screen and (max-width: 360px) {
    bottom: 2rem; 
   
}



`;

export const NavMenu = styles.ul`
display: flex;
align-items: center;
list-style: none;
text-align: center;


@media screen and (max-width: 768px) {
    display: none;
}
`;

export const NavItem = styles.li`
height: 80px;
`;

export const NavLinks = styles(ScrollLink)`
color: #8A93AE;
display: flex;
align-items: center;
text-decoration: none;
padding: 0 1rem;
height: 100%;
cursor: pointer;
font-size: 20px;

&.active {
    border-bottom: 3px solid red;
}
`;

export const NavLinksR = styles.a`
color: #8A93AE;
display: flex;
align-items: center;
text-decoration: none;
padding: 0 1rem;
height: 100%;
cursor: pointer;
font-size: 20px;

&.active {
    border-bottom: 3px solid red;
}
`;
