import styles from "styled-components";
import { Link as ScrollLink } from "react-scroll";

export const HeroContainer = styles.div`
background:#0A093D;
display: flex;
justify-content: left;
height:1100px;
max-height: 1100px;
width: 100%;
position: relative;
z-index: 1;
align-items: center;
margin:auto;

@media all and (min-width: 1366px){
    height: 100vh;
    max-height: 100vh;
}

`;

export const HeroContent = styles.div`

display: flex;
flex-direction: column;
color: #d91616;
margin-top: 0px;
padding-left: 171px;

height:100%;
width:100%;
@media all and (max-width: 1017px){
    padding-left: 60px;
}
@media all and (max-width: 947px){
    padding-left: 60px;
}
@media all and (max-width:529px){
    padding-left: 4vw;
}
@media all and (min-width:697px){

padding-top:30px;

}
@media all and (min-width: 1366px){
max-width: 1200px;

display: flex;
flex-direction: column;
color: #d91616;
margin:auto;
margin-top: 0px;
padding-left: 171px;
padding-top: 0px;
height:90vh;
width:100%;
justify-content:center;
}


`;

export const HeroH5 = styles.h5`
color:#8A93AE ;
font-size: 24.03px;
margin-bottom: 0.2rem;


@media all and (max-width: 412px){
    font-size: 20px;
    margin-bottom: 20px;
    padding-left: 30vw;
    
}
@media all and (max-width: 326px){
    padding-left: 0;
    text-align:center;
    font-size: 7vw;
}


`;

export const HeroH3 = styles.h3`
color: #CED7F3;
font-size: 44px;
margin-bottom: 1.2rem;

@media screen and (max-width: 474px){
   font-size: 30.03px;
}
@media all and (max-width: 412px){
    font-size: 35px;
    margin-bottom: 20px;
    padding-left: 0;
}
@media all and (max-width: 361px){
    font-size: 32px;
    text-align:center;
}
@media all and (max-width: 330px){
    font-size: 25px;
}
@media all and (max-width: 261px){
    font-size: 9.5vw;
}
`;

export const HeroSection = styles.div`
display: flex;
margin-bottom: 1.2rem;

@media all and (max-width: 412px){
    margin-bottom: 20px;
}
`;

export const Vertical = styles.div`
border-left: 12px solid #BF2121;
height: auto;

@media screen and (max-width: 474px){
  border-left: 4px solid #BF2121; 
}
`;

export const HeroP = styles.h1`
color:#8A93AE;
bottom: 1.2rem;
padding: 0;
margin-left: 20px;

max-width: 750px;
height: auto;
margin-top: 0px;
position: relative;
transition: all 0.3s ease-in;
font-weight: bold;
@media all and (max-width: 290px){
    font-size: 13.6vw;
}

@media (min-width: 291px) and (max-width: 342px){
    font-size: 40.76px;
}

@media (min-width: 343px) and (max-width: 412px){
   font-size: 47.76px;
   width: 100%;
   margin-left: 4vw;
}

@media (min-width: 413px) and (max-width: 486px){
    font-size: 57.76px;
}

@media (min-width: 487px) and (max-width: 607px ){
    font-size: 60px;
    

}

@media all and (min-width: 608px){
 font-size: 60px;
}
@media all and (min-width:1366px){
font-size: 79.86px;
line-height: 89px;
}






`;

export const HeroSmallP = styles.p`
color:#CED7F3;
font-size: 21.43px;
max-width: 659.79px;
margin-bottom: 1.2rem;


@media all and (max-width: 768px) {
    width: 100%;
}

@media all and (max-width: 412px){
   font-size: 21.43px;
   width: 90vw;
}
@media all and (max-width: 294px){
    font-size: 7vw;
}
`;

export const HeroContactWrap = styles.div`
display: flex;
@media all and (max-width: 412px) {
    justify-content: center;
}

`;
export const Button = styles(ScrollLink)`
background-color: #BF2121;
display: flex;
outline: none;
border-radius: 10px;
color: white;
width: 158px;
height: 57.52px;
font-size: 16.21px;
text-align: center;
justify-content: center;
align-items: center;
`;
