import React from "react";
import {
  ContainerWrapper,
  ContactWrapper,
  ContactHead,
  ContactText,
  ContactBtn,
} from "./contactStyle";


const Contact = () => {
  return (
    <ContainerWrapper id="contact">
      <ContactWrapper>
        <ContactHead>Get In Touch</ContactHead>

        <ContactText>
          For more information, you can contact me using the link below.
        </ContactText>

        
        <ContactBtn to='/contactme'>Say Hello</ContactBtn>
      </ContactWrapper>
    </ContainerWrapper>
  );
};

export default Contact;
