import styles from "styled-components";
import { Tabs, TabList, Tab, TabPanel } from "react-tabs";
import { FaChevronLeft, FaChevronRight } from "react-icons/fa";
export const ExWrap = styles.section`
background-color: #0A093D;

margin: 0;
display:flex;
align-items:center;

@media all and (max-width: 412px){
    width: 100%;
    height: 100vh;
    min-height: 1000px;
}

@media all and (min-width: 768px){
    background-color: #0A093D;
min-height: 646px;
height:100%;
margin: 0;
display:flex;
align-items:center;
width: 100%;
 overflow: hidden;
 padding-bottom:30px;
}
@media all and (min-width:1366px){
   background-color: #0A093D;
height: 646px;
margin: 0;
display:flex;
align-items:center;
width: 100%; 
padding-bottom:100px;
}
`;

export const ExContentWrap = styles.div`

height: 481px;
margin-left: 180px;
@media all and (max-width: 1017px){
    margin-left: 60px;
}
@media all and (max-width: 947px){
    margin-left: 60px;
    height: 100%;
}
@media all and (max-width: 768px) {
     margin-left: 1px;
    height: 100%;
    padding: 50px 0;
    width: 100vw;
}
@media all and (max-width: 412px){
    margin-left: 1px;
    height: 100%;
    padding: 50px 0;
    width: 100vw;
}

@media all and (min-width:1366px){
    width: 976px;
height: 481px;
margin: auto;
}

`;

export const HeaderSection = styles.div`
display: flex;
@media all and (max-width: 907px){
      display: block;
  text-align: center;
  justify-content: center;
}
@media all and (max-width: 768px){
    display: block;
  text-align: center;
  justify-content: center;
}

@media all and (max-width: 412px){
    display: block;
  text-align: center;
  justify-content: center;
}
`;

export const ExH1 = styles.h3`
color: white;
margin-right: 20px;
font-size: 44px;
`;

export const LineWrap = styles.div`
width: 740px;
position: relative;
top: 2.5rem;
margin: 0;
padding: 0;

@media all and (max-width:1160px){
    width: 70%;
}
@media all and (max-width: 1116px){
    width: 58%;
  
}
@media all and (max-width: 907px){
    display: none;
}
@media all and (max-width: 768px){
    display: none;
}
@media all and (max-width: 412px){
   display: none;
}
`;

export const Linehr = styles.hr`
border-color: #C4C4C4;
`;

export const TabsSection = styles(Tabs)`
display: flex;
margin-top: 20px;
width: 918px;
height: 369px;
position: relative;
top: 0;
bottom: 0;
left: 0;
right: 0;

@media all and (max-width: 907px){
  display: grid;
   width: auto;
   height: auto;  
}
@media all and (max-width: 768px){
    display: grid;
   width: auto;
   height: auto;
}
@media all and (max-width: 412px){
   display: grid;
   width: auto;
   height: auto;
}
`;

export const ButtonLeft = styles(FaChevronLeft)`
display: none;

@media all and (max-width: 412px){
}
`;

export const ButtonRight = styles(FaChevronRight)`
color: white;
display: none;
@media all and (max-width: 412px){
    transition: all 1s ease-in;
    display: block;
    position: absolute;
    left: 24rem;
    top: 2rem;
}
`;

export const TabsHead = styles(TabList)`

list-style: none;
position: relative;
transition: all 0.2s ease-in;
padding-left: 0;
@media all and (max-width: 1020px){
    margin-right: 90px;
}


@media all and (max-width: 907px){
     display: grid;
   width: 100%;
   height: auto;
   grid-template-columns: 1fr 1fr 1fr;
   grid-template-row: 1fr;
   overflow: auto; 
   margin-bottom:20px;
}
@media all and (max-width: 768px){
    display: grid;
   width: 100%;
   height: auto;
   white-space: nowrap;
   grid-template-columns: 1fr 1fr 1fr;
   grid-template-row: 1fr;
   overflow: auto;
}
@media all and (max-width:589px){
  overflow:hidden;
   display: grid;
   width: 90%;
   height: auto;
   grid-template-columns: 1fr 1fr 1fr;
   grid-template-row: 1fr;
   overflow: auto;
   white-space: nowrap;
   transition:  all 0.5s ease-out;
   -moz-transition    : all 0.5s ease-out;
  -o-transition      : all 0.5s ease-out;
  -webkit-transition : all 0.5s ease-out;
  margin:Auto;
   margin-bottom:20px;
}

@media all and (min-width: 768px){
    margin-right: 90px;
list-style: none;
position: relative;
transition: all 0.2s ease-in;
padding-left: 0;
}
`;

export const TabsTitle = styles(Tab)`
width: 203px;
height: 50px;
color: #8A93AE;
border-left: 3px solid #8A93AE;
padding-top: 30px;
padding-bottom:50px;
text-align:center;
font-size: 20px;
outline: none;
cursor: pointer;

&:hover {
    background: rgba(206,215,243, 0.1);
    color: #F20F22;
    transition: all 0.2s ease-in-out;
    border-left: 3px solid red;
}
&:active {
     background: rgba(206,215,243, 0.1);
    color: #F20F22;
    transition: all 0.2s ease-in-out;
    border-left: 3px solid red;
}

@media all and (max-width: 907px){
    border-left: none;
    background: rgba(48, 47, 91, 0.85);
    margin-left:10px;
    height: 30px;
   &:hover {
    background: rgba(206,215,243, 0.1);
    color: #F20F22;
    transition: all 0.2s ease-in-out;
    border-bottom: 3px solid red;
    border-left: none;
}
&:active {
     background: rgba(206,215,243, 0.1);
    color: #F20F22;
    transition: all 0.2s ease-in-out;
    border-bottom: 3px solid red;
    border-left: none;
}
}
@media all and (max-width: 768px) {
        border-left: none;
    

   &:hover {
    background: rgba(206,215,243, 0.1);
    color: #F20F22;
    transition: all 0.2s ease-in-out;
    border-bottom: 3px solid red;
    border-left: none;
}
&:active {
     background: rgba(206,215,243, 0.1);
    color: #F20F22;
    transition: all 0.2s ease-in-out;
    border-bottom: 3px solid red;
    border-left: none;
}
}
@media all and (max-width: 412px){

    border-left: none;
    

   &:hover {
    background: rgba(206,215,243, 0.1);
    color: #F20F22;
    transition: all 0.2s ease-in-out;
    border-bottom: 3px solid red;
    border-left: none;
}
&:active {
     background: rgba(206,215,243, 0.1);
    color: #F20F22;
    transition: all 0.2s ease-in-out;
    border-bottom: 3px solid red;
    border-left: none;
}
}
`;

