import React, { useState } from "react";

import {
  ContainerWrapper,
  ContentWrapper,
  MyWorkwrapper,
  BoxFooter,
  BoxWrapper,
} from "./styles";
import { WorkData } from "../../data/allworks";
import VideoLoader from "../../assets/other/videoLoad.gif";

import { LazyLoadImage } from "react-lazy-load-image-component";

import {
  Box,
  Grid,
  GridItem,
  VStack,
  Text,
  Link,
  Button,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalBody,
  ModalCloseButton,
  useDisclosure,
} from "@chakra-ui/react";

const AllWorks = () => {
  const [activeDrop, setActiveDrop] = useState("");
  const { isOpen, onOpen, onClose } = useDisclosure();
  const setDropVisible = (data) => {
    setActiveDrop(data);
  };

  const setDropInvisible = () => {
    setActiveDrop("");
  };

  return (
    <ContainerWrapper id="work">
      <ContentWrapper>
        <MyWorkwrapper>
          <Modal
            isOpen={isOpen}
            onClose={onClose}
            isCentered
            blockScrollOnMount={true}
            maxW="100%"
            maxH="100vh"
            motionPreset="slideInBottom"
            preserveScrollBarGap
          >
            <ModalOverlay bg="rgba(10, 9, 61, 0.7)" />

            <ModalContent maxW={900} maxH={700}>
              <ModalCloseButton
                color="red"
                top={["-9px", "-3px"]}
                fontSize={["15px", "20px"]}
                _focus="none"
                variant="outline"
                _active={{
                  bg: "transparent",
                  transform: "scale(0.98)",
                  borderColor: "none",
                }}
              />
              <ModalBody py={7} overflow="hidden">
                <video
                  autoPlay={true}
                  src="https://ik.imagekit.io/dk30bxu4rix/Website/assets/video/time-extended_1__aZVESuIUC.mp4"
                  loop
                  playsinline
                  poster={VideoLoader}
                  type="video/mp4"
                  alt="about-video"
                  count={2}
                  w="100%"
                />
              </ModalBody>
            </ModalContent>
          </Modal>
          <Grid
            templateColumns={[
              "repeat(1, 1fr)",
              "repeat(1, 1fr)",
              "repeat(2, 1fr)",
              "repeat(2, 1fr)",
              "repeat(3, 1fr)",
            ]}
            gap={6}
            h="100%"
          >
            {WorkData.map((data, index) => (
              <GridItem
                w={["319.27px"]}
                h="278.51px"
                bg="transparent"
                onClick={() => setDropVisible(data.projectName)}
                onMouseOver={() => setDropVisible(data.projectName)}
                onMouseLeave={setDropInvisible}
                position="relative"
                key={index}
              >
                <BoxWrapper
                  className={activeDrop === data.projectName ? "active" : null}
                >
                  <BoxFooter>
                    {data.type === "uploaded" && (
                      <Link
                        href={data.link}
                        fontSize="sm"
                        color="#ffffff"
                        style={{ textDecoration: "none", fontStyle: "italic" }}
                        isExternal
                      >
                        Visit site
                      </Link>
                    )}

                    {data.type === "demo" && (
                      <>
                        <Button
                          fontSize="sm"
                          color="#ffffff"
                          _focus="none"
                          style={{
                            textDecoration: "none",
                            fontStyle: "italic",
                            fontWeight: "300",
                          }}
                          variant="none"
                          pt="20px"
                          m={0}
                          onClick={onOpen}
                        >
                          See Demo
                        </Button>
                      </>
                    )}

                    {data.type === "soon" && (
                      <Text
                        fontSize="sm"
                        color="#ffffff"
                        style={{ textDecoration: "none", fontStyle: "italic" }}
                        isExternal
                      >
                        Coming soon
                      </Text>
                    )}
                  </BoxFooter>
                </BoxWrapper>
                <VStack>
                  <Box>
                    <LazyLoadImage
                      width="319.27px"
                      height="237.59px"
                      objectFit="cover"
                      effect="blur"
                      src={data.imageSrc}
                      alt={data.projectName}
                    />
                  </Box>

                  {activeDrop !== data.projectName && (
                    <Text fontSize="sm" color="#ffffff">
                      {data.projectName}
                    </Text>
                  )}
                </VStack>
              </GridItem>
            ))}
          </Grid>
        </MyWorkwrapper>
      </ContentWrapper>
    </ContainerWrapper>
  );
};

export default AllWorks;
