import React from "react";
import {
  Container,
  Flex,
  VStack,
  Heading,
  Text,
  useToast,
} from "@chakra-ui/react";

import axios from "axios";

import { Formik, Field } from "formik";
import * as yup from "yup";
import ContactForm from "../common/Forms/contact";

const validation = yup.object().shape({
  name: yup
    .string()
    .min(3, "Must be more than 3 characters")
    .max(15, "Must be lass than 15 characters")
    .required("Name is required"),

  number: yup
    .string()
    .min(10, "Must be more than 9 digits")
    .required("Number is required"),

  email: yup
    .string()
    .email("Invalid email address")
    .required("email is required"),
  message: yup
    .string()
    .min(4, "Must be more than 4 characters")
    .max(200, "Must be less than 15 characters")
    .required("message is required"),
});

const ContactMe = () => {
  const toast = useToast();
  return (
    <Container
      maxWidth="full"
      w="full"
      p={0}
      mr={0}
      ml={0}
      bg="#0A093D"
      id="contactMe"
    >
      <Container
        maxWidth="920px"
        justifyItem="center"
        p={0}
        pt={"28px"}
        pb={"150px"}
        m="auto"
      >
        <Flex h="100vh" py={10}>
          <VStack
            maxW="600px"
            w="full"
            h="full"
            p={10}
            spacing={10}
            m="auto"
            alignItems="flex-start"
          >
            <VStack spacing={3} alignItems="flex-start">
              <Heading color="#ffffff" size="2xl">
                Get in touch
              </Heading>
              <Text color="#ffffff">Send a Request</Text>
            </VStack>

            <Formik
              initialValues={{
                name: "",
                number: "",
                email: "",
                message: "",
                platform: "jk-website",
              }}
              validationSchema={validation}
              onSubmit={(values, helpers) => {
                axios
                  .post(
                    "https://email-pot-jk.herokuapp.com/send/mailjk",
                    values
                  )
                  .then(() => {
                    toast({
                      position: "top",
                      title: "Message sent successfully",
                      status: "success",
                      duration: 10000,
                      isClosable: true,
                    });

                    helpers.resetForm();
                    helpers.setSubmitting(false);
                   
                  })
                  .catch((error) => {
                    let errorArray = [];
                    errorArray.push(error);

                    let message;
                    if (errorArray.length !== 0 && errorArray[0].response) {
                      message = errorArray[0].response.data;
                    } else if (
                      errorArray.length !== 0 &&
                      !errorArray[0].response
                    ) {
                      message = errorArray[0].message;
                    }
                    toast({
                      position: "top",
                      title: message,
                      status: "error",
                      duration: 10000,
                      isClosable: true,
                    });

                    helpers.resetForm();
                    helpers.setSubmitting(false);
                  });
              }}
            >
              {({ errors, handleSubmit, touched }) => (
                <>
                  <ContactForm
                    errors={errors}
                    touched={touched}
                    submit={handleSubmit}
                    Field={Field}
                  />
                </>
              )}
            </Formik>
          </VStack>
        </Flex>
      </Container>
    </Container>
  );
};

export default ContactMe;
