import React from 'react';

import {
  HeroContainer,
  HeroContent,
  HeroH5,
  HeroH3,
  HeroSection,
 Vertical,
  HeroP,
  HeroSmallP,
  HeroContactWrap,
  Button
} from "./heroStyle";
const Hero = () => {
    return (
      <HeroContainer>
        <HeroContent>
          <HeroH5>Hi, my name is</HeroH5>
          <HeroH3>Joshua Kimbareeba</HeroH3>
          <HeroSection>
            <Vertical></Vertical>
            <HeroP>I develop software around javaScript technologies</HeroP>
          </HeroSection>
          <HeroSmallP>
            I am a software developer based in uganda, I specialize in building
            websites, Backend API, Applications...
          </HeroSmallP>
          <HeroContactWrap>
            <Button to="contact">Get In Touch</Button>
          </HeroContactWrap>
        </HeroContent>
      </HeroContainer>
    );
}

export default Hero
