import styles from 'styled-components';
import { Link } from 'react-router-dom';
export const ContainerWrapper = styles.section`
background: #0A093D;
display:flex;
position: relative;
align-items:center;
z-index: 3;
width: 100%;
padding: 0;
@media screen and (max-width: 1116px){
    width: 100vw;
    overflow: hidden;
}
@media all and (max-width: 768px){
    width: 100vw;
}
@media all and (max-width: 412px){
    width: 100vw;
   
}
`;

export const ContactWrapper = styles.div`


      width: 100%;
    height: 381px;
text-align:center;
display:flex;
flex-direction:column;
padding-top: 50px;
align-items:center;
justify-content:center;
margin:auto;

@media all and (min-width: 768px){
      width: 100%;
    height: 381px;
text-align:center;
display:flex;
flex-direction:column;
padding-top: 50px;
align-items:center;
justify-content:center;
margin:auto;
}

@media all and (min-width:1366px){
    width: 1006px;
height: 381px;
text-align:center;
display:flex;
flex-direction:column;
padding-top: 50px;
align-items:center;

margin:auto;
}
`;

export const ContactHead = styles.h3`
font-size: 30px;
color: #CED7F3;
line-height: 41px;

@media all and (min-width:768px){
   font-size: 44px;
color: #CED7F3;
line-height: 41px; 
}

`;

export const ContactText = styles.p`
font-size: 15px;
color: #CED7F3;
margin:auto;
line-height: 24px;
font-weight: semibold;
margin-bottom: 35px;
margin-top: 10px;
width:90%;

@media all and (min-width:768px){
    font-size: 17px;
    width:100%;
}
`;

export const ContactBtn = styles(Link)`
text-decoration: none;
width: 220px;
height: 66px;
border-radius: 12px;
border: 2px solid #F20F22;
padding: 12px 40px;
color: #F20F22;
font-size: 22px;

`;