import styles from 'styled-components';

export const ContainerWrapper = styles.footer`
width: 100%;
height: 369.34;
background-color: #0A093D;
position: relative;
z-index: 3;
background-size: cover;
overflow:hidden;
@media screen and (max-width: 1116px){
    width: 100vw;
    overflow: hidden;
}
@media all and (max-width: 768px){
    width: 100vw;
    padding-top: 10px;
}
@media all and (max-width: 412px) {
    width: 100vw;
    padding-top: 10px;
}
`;

export const FooterWrapper = styles.div`
background-size: cover;
position:relative;
width: 100%;
padding: 0;
height: 369.34px;
display: flex;
align-items: bottom;



`;
export const FooterCover = styles.div`
width: 100vw;
height: 366.88px;
position:relative;
z-index: 3;

padding-top: 20px;
display: flex;
align-items: center;
justify-content:center;

@media all and (min-width: 1320px){
    height:100%;
}
@media all and (min-width: 1441px){
    background: linear-gradient(178.49deg, #0B2573 -15.05%, #0A093D 23.38%, #080F26 103.16%, #080F26 103.16%);
}
@media all and (max-width: 1343px){
 
}
@media all and (max-width: 768px){
    width: 100vw;
  
}
@media all and (max-width: 412px) {
    width: 100vw;
   
   
  
}
`;

export const ImageWrapper = styles.div`
width:100%;
 height: 366.88px;
display:flex;
aligh-items: flex-end;

img {
    width:100%;
    height:100%;
    object-fit:cover;
}

@media all and (min-width: 1315px){
    height:400px;

    img {
    width:100%;
    height:100%;
    object-fit:contain;
}
}

@media all and (min-width: 1320px){
   height:400px;

    img {
    width:100%;
    height:100%;
    object-fit:contain;
}
}

@media all and (min-width: 1441px){
    
    img {
        display:none;
    }
}

`;
