import { FaTimes } from 'react-icons/fa';
import styles from 'styled-components';
import {Link as ScrollLink} from "react-scroll";
export const SideMenuContainer = styles.aside`
position: fixed;
z-index: 999;
width: 100%;
height: 100%;
background:#0A093D;
display: grid;
align-items: center;
top: 0;
left: 0;
transition: 0.2s ease-in-out;
opacity: ${({sideNav})=>(sideNav ? '100%' : '0')};
top: ${({sideNav})=> ( sideNav ? '0' : '-100%')};
`;

export const Icon = styles.div`
position: absolute;
top: 1.2rem;
right: 1.5rem;
background: transparent;
font-size: 2rem;
cursor: pointer;
outline:none;
`;
export const CloseIcon = styles(FaTimes)`
color: #d91616;
`;

export const SideMenuWrap = styles.div`
color: black;

`;
export const SideMenuList = styles.ul`
display: grid;
grid-template-columns: 1fr;
grid-template-rows: repeat(5, 80px);
text-align: center;
@media screen and (max-width: 480px){
    grid-template-rows: repeat(6, 60px);
}
`;
 
export const SideItem = styles(ScrollLink)`
text-decoration: none;
font-size: 1.5rem;
list-style: none;
color: #8A93AE;
cursor: pointer;
display: flex;
justify-content: center;
align-items: center;
transition: 0.2s ease-in-out;

&: hover {
    color: #CED7F3;
    background: rgba(138, 147, 174, 0.1);
    transition: 0.2s ease-in-out;
}

`;

export const NavLinksR = styles.a`
color: #8A93AE;
display: flex;
align-items: center;
text-decoration: none;
text-decoration: none;
font-size: 1.5rem;
list-style: none;
color: #8A93AE;
cursor: pointer;
display: flex;
justify-content: center;
align-items: center;
transition: 0.2s ease-in-out;

&: hover {
    color: #CED7F3;
    background: rgba(138, 147, 174, 0.1);
    transition: 0.2s ease-in-out;
}
`;