import React from 'react'
import {
  SideMenuContainer,
  Icon,
  CloseIcon,
  SideMenuWrap,
  SideMenuList,
  SideItem,
  NavLinksR,
} from "./sideElements";
const SideMenu = ({sideNav, toggle}) => {
    return (
      <SideMenuContainer sideNav={sideNav} onClick={toggle}>
        <Icon>
          <CloseIcon />
        </Icon>
        <SideMenuWrap>
          <SideMenuList>
            <SideItem to="about" onClick={toggle}>
              About
            </SideItem>
            <SideItem to="experience" onClick={toggle}>
              Experience
            </SideItem>
            <SideItem to="work" onClick={toggle}>
              Work
            </SideItem>
            <SideItem to="contact" onClick={toggle}>
              Contact
            </SideItem>
            <NavLinksR
              href="https://drive.google.com/file/d/1pI_bx-DZfUeUOYiRxVfx-E68QEIWAo4o/view?usp=sharing"
              target="_blank"
            >
              Resume
            </NavLinksR>
          </SideMenuList>
        </SideMenuWrap>
      </SideMenuContainer>
    );
}

export default SideMenu
