import React from "react";
import { animateScroll as scroll } from "react-scroll";
import logo from "../../assets/images/logo/JK.svg";
import { FaBars } from "react-icons/fa";
import {
  NavWraps,
  Nav,
  NavbarContainer,
  NavLogo,
  LogoImage,
  MobileIcon,
  NavMenu,
  NavItem,
  NavLinks,
  NavLinksR,
} from "./elements";
const Navbar = ({ toggle }) => {
  const homeToggle = () => {
    scroll.scrollToTop();
  };
  return (
    <NavWraps>
      <Nav>
        <NavbarContainer>
          <NavLogo to="/" onClick={homeToggle}>
            <LogoImage src={logo} />
          </NavLogo>
          <MobileIcon onClick={toggle}>
            <FaBars />
          </MobileIcon>
          <NavMenu>
            <NavItem>
              <NavLinks
                to="about"
                smooth={true}
                duration={500}
                spy={true}
                exact="true"
                offset={-80}
              >
                About
              </NavLinks>
            </NavItem>
            <NavItem>
              <NavLinks
                to="experience"
                smooth={true}
                duration={500}
                spy={true}
                exact="true"
                offset={-80}
              >
                Experience
              </NavLinks>
            </NavItem>
            <NavItem>
              <NavLinks
                to="work"
                smooth={true}
                duration={500}
                spy={true}
                exact="true"
                offset={-80}
              >
                Work
              </NavLinks>
            </NavItem>
            <NavItem>
              <NavLinks
                to="contact"
                smooth={true}
                duration={500}
                spy={true}
                exact="true"
                offset={-80}
              >
                Contact
              </NavLinks>
            </NavItem>
            <NavItem>
              <NavLinksR
                href="https://drive.google.com/file/d/1pI_bx-DZfUeUOYiRxVfx-E68QEIWAo4o/view?usp=sharing"
                target="_blank"
              >
                Resume
              </NavLinksR>
            </NavItem>
          </NavMenu>
        </NavbarContainer>
      </Nav>
    </NavWraps>
  );
};

export default Navbar;
